.l-main {
  max-width: 1400px;
  margin: 0 auto;
}

.c-btn {
  text-decoration: none;
  color: #000000;
}

.c-btn__inner {
  border-radius: 4px;
}

.c-btn-01 {
  display: block;
  width: 380px;
  height: 60px;
  transition: all 2.5s 1s;
  position: relative;
  letter-spacing: 4px;
  border-radius: 4px;
}

.c-btn-01::before, .c-btn-01::after {
  content: "";
  width: 1px;
  height: 21px;
  background-color: #000000;
  border-radius: 8px;
  position: absolute;
  top: 0;
  transition: all 0.3s;
}

.c-btn-01::before {
  transform: rotate(20deg) scale(0);
  transform-origin: top left;
  left: 6px;
}

.c-btn-01::after {
  transform: rotate(-20deg) scale(0);
  transform-origin: top right;
  right: 6px;
}

.c-btn-01:hover::before, .c-btn-01:hover::after, .c-btn-01:focus::before, .c-btn-01:focus::after {
  transform: scale(1);
}

.c-btn-01:hover::before, .c-btn-01:focus::before {
  transform: rotate(25deg);
}

.c-btn-01:hover::after, .c-btn-01:focus::after {
  transform: rotate(-25deg);
}

.c-btn-01:hover .c-btn-01__box, .c-btn-01:focus .c-btn-01__box {
  transform: translateY(20px) scale(1.03);
  z-index: 2000;
}

.c-btn-01__box {
  background-color: #ffffff;
  border: 1px solid;
  border-radius: 4px;
  display: block;
  width: inherit;
  height: inherit;
  text-align: center;
  transition: all 0.5s;
  position: relative;
}

.c-btn-01__inner {
  width: calc(100% - 16px);
  padding: 8px 0;
  background-color: #ffffff;
  border: 1px solid;
  border-radius: 4px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.c-btn-01__inner::after {
  display: block;
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: solid 2px #00000080;
}

.c-btn-02 {
  display: block;
  width: 200px;
  height: 100%;
  transition: all 2.5s 1s;
  position: relative;
}

.c-btn-02::before, .c-btn-02::after {
  content: "";
  width: 1px;
  height: 10px;
  background-color: #000000;
  position: absolute;
  top: 0;
  transition: all 0.3s;
}

.c-btn-02::before {
  transform: rotate(20deg) scale(0);
  transform-origin: top left;
  left: 5px;
}

.c-btn-02::after {
  transform: rotate(-20deg) scale(0);
  transform-origin: top right;
  right: 5px;
}

.c-btn-02:hover::before, .c-btn-02:hover::after {
  transform: scale(1);
}

.c-btn-02:hover::before {
  transform: rotate(25deg);
}

.c-btn-02:hover::after {
  transform: rotate(-25deg);
}

.c-btn-02:hover .c-btn-02__inner {
  transform: translateY(10px) scale(1.03);
  z-index: 2000;
}

.c-btn-02__inner {
  background-color: #ffffff;
  border: 1px solid #000000;
  display: block;
  padding: 8px 10px 4px;
  position: relative;
  text-align: center;
  transition: all 0.5s;
}

.c-btn-02__inner::after {
  display: block;
  content: "";
  position: absolute;
  top: 0;
  left: 50%;
  background-color: #505050;
  transform: translateX(-50%);
  width: 20%;
  height: 10px;
  border-bottom-right-radius: 6px;
  border-bottom-left-radius: 6px;
  border: solid 2px #505050;
}

.c-btn_03 {
  position: relative;
  width: 300px;
  display: block;
  border: 1px solid #000000;
  text-align: center;
  padding: 12px 0;
  transition: all 0.5s;
}

.c-btn_03:before {
  display: block;
  content: "";
  position: absolute;
  top: -7px;
  width: 100%;
  /* 幅 */
  height: 0px;
  /* y位置 */
  border-bottom: 6px solid #727272;
  /* 高さ */
  border-left: 15px solid transparent;
  /* 左側の角度 */
  border-right: 15px solid transparent;
  /* 右側の角度*/
}

.c-btn_03:hover {
  transform: scale(1.02);
}

